"use client"

import { useState, useEffect } from "react"
import { useRouter } from "next/navigation"
import { createClient } from "@/lib/supabase/client"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table"
import { Badge } from "@/components/ui/badge"
import {
  Search,
  Filter,
  Heart,
  ExternalLink,
  X,
  Calendar,
  Building2,
  Briefcase,
  MapPin,
  ChevronLeft,
  ChevronRight,
  MessageSquare,
} from "lucide-react"
import { JobFeedbackModal } from "@/components/job-feedback-modal"
import { format } from "date-fns"
import { ptBR } from "date-fns/locale"

interface Job {
  id: string
  title: string
  company_name: string
  job_area: string
  country: string
  city: string | null
  apply_url: string
  posted_at: string
}

interface SentJob {
  id: string
  job_id: string
  sent_at: string
  view_status: string
  jobs: Job
}

interface Filters {
  search: string
  dateRange: string
  company: string
  area: string
  status: string
}

const ITEMS_PER_PAGE = 10

export default function JobsHistoryPage() {
  const router = useRouter()
  const [jobs, setJobs] = useState<SentJob[]>([])
  const [allJobs, setAllJobs] = useState<SentJob[]>([])
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)
  const [filters, setFilters] = useState<Filters>({
    search: "",
    dateRange: "all",
    company: "all",
    area: "all",
    status: "all",
  })
  const [currentPage, setCurrentPage] = useState(1)
  const [companies, setCompanies] = useState<string[]>([])
  const [areas, setAreas] = useState<string[]>([])
  const [feedbackModalOpen, setFeedbackModalOpen] = useState(false)
  const [selectedJobForFeedback, setSelectedJobForFeedback] = useState<{ jobId: string; sentJobId: string; jobTitle: string } | null>(null)

  useEffect(() => {
    fetchJobs()
  }, [])

  useEffect(() => {
    applyFilters()
  }, [filters, allJobs])

  const fetchJobs = async () => {
    const supabase = createClient()

    try {
      const {
        data: { user },
      } = await supabase.auth.getUser()

      if (!user) {
        router.push("/auth/login")
        return
      }

      // Buscar histórico de vagas enviadas com dados das vagas
      const { data: sentJobsData, error: sentJobsError } = await supabase
        .from("sent_jobs")
        .select(
          `
          id,
          job_id,
          sent_at,
          view_status,
          jobs (
            id,
            title,
            company_name,
            job_area,
            country,
            city,
            apply_url,
            posted_at
          )
        `,
        )
        .eq("user_id", user.id)
        .order("sent_at", { ascending: false })

      if (sentJobsError) throw sentJobsError

      const formattedJobs = (sentJobsData || []).map((item: any) => ({
        id: item.id,
        job_id: item.job_id,
        sent_at: item.sent_at,
        view_status: item.view_status,
        jobs: item.jobs,
      }))

      setAllJobs(formattedJobs)
      setJobs(formattedJobs)

      // Extrair empresas e áreas únicas para filtros
      const uniqueCompanies = Array.from(new Set(formattedJobs.map((j: SentJob) => j.jobs?.company_name).filter(Boolean))) as string[]
      const uniqueAreas = Array.from(new Set(formattedJobs.map((j: SentJob) => j.jobs?.job_area).filter(Boolean))) as string[]

      setCompanies(uniqueCompanies.sort())
      setAreas(uniqueAreas.sort())
    } catch (err) {
      console.error("[Jobs History] Erro:", err)
      setError("Erro ao carregar histórico de vagas. Tente novamente mais tarde.")
    } finally {
      setLoading(false)
    }
  }

  const applyFilters = () => {
    let filtered = [...allJobs]

    // Filtro de busca (texto)
    if (filters.search) {
      const searchLower = filters.search.toLowerCase()
      filtered = filtered.filter(
        (job) =>
          job.jobs?.title?.toLowerCase().includes(searchLower) ||
          job.jobs?.company_name?.toLowerCase().includes(searchLower) ||
          job.jobs?.job_area?.toLowerCase().includes(searchLower),
      )
    }

    // Filtro de data
    if (filters.dateRange !== "all") {
      const now = new Date()
      let cutoffDate = new Date()

      switch (filters.dateRange) {
        case "today":
          cutoffDate.setHours(0, 0, 0, 0)
          break
        case "week":
          cutoffDate.setDate(now.getDate() - 7)
          break
        case "month":
          cutoffDate.setDate(now.getDate() - 30)
          break
        case "3months":
          cutoffDate.setDate(now.getDate() - 90)
          break
      }

      filtered = filtered.filter((job) => new Date(job.sent_at) >= cutoffDate)
    }

    // Filtro de empresa
    if (filters.company && filters.company !== "all") {
      filtered = filtered.filter((job) => job.jobs?.company_name === filters.company)
    }

    // Filtro de área
    if (filters.area && filters.area !== "all") {
      filtered = filtered.filter((job) => job.jobs?.job_area === filters.area)
    }

    // Filtro de status
    if (filters.status !== "all") {
      filtered = filtered.filter((job) => job.view_status === filters.status)
    }

    setJobs(filtered)
    setCurrentPage(1) // Reset para primeira página ao filtrar
  }

  const handleFavorite = async (jobId: string) => {
    try {
      const supabase = createClient()
      const {
        data: { user },
      } = await supabase.auth.getUser()

      if (!user) return

      // Verificar se já está favoritado
      const { data: existing } = await supabase.from("favorite_jobs").select("id").eq("user_id", user.id).eq("job_id", jobId).maybeSingle()

      if (existing) {
        // Remover dos favoritos
        await supabase.from("favorite_jobs").delete().eq("user_id", user.id).eq("job_id", jobId)
      } else {
        // Adicionar aos favoritos
        await supabase.from("favorite_jobs").insert({
          user_id: user.id,
          job_id: jobId,
        })
      }

      // Atualizar status
      await supabase
        .from("sent_jobs")
        .update({ view_status: existing ? "viewed" : "favorited" })
        .eq("user_id", user.id)
        .eq("job_id", jobId)

      fetchJobs() // Recarregar dados
    } catch (error) {
      console.error("[Jobs History] Erro ao favoritar:", error)
    }
  }

  const handleDismiss = async (jobId: string) => {
    try {
      const supabase = createClient()
      const {
        data: { user },
      } = await supabase.auth.getUser()

      if (!user) return

      await supabase
        .from("sent_jobs")
        .update({ view_status: "dismissed" })
        .eq("user_id", user.id)
        .eq("job_id", jobId)

      fetchJobs() // Recarregar dados
    } catch (error) {
      console.error("[Jobs History] Erro ao ignorar:", error)
    }
  }

  const handleApply = async (jobId: string, applyUrl: string) => {
    try {
      const supabase = createClient()
      const {
        data: { user },
      } = await supabase.auth.getUser()

      if (!user) return

      // Atualizar status para "applied"
      await supabase
        .from("sent_jobs")
        .update({ view_status: "applied" })
        .eq("user_id", user.id)
        .eq("job_id", jobId)

      // Abrir URL de candidatura
      window.open(applyUrl, "_blank")

      fetchJobs() // Recarregar dados
    } catch (error) {
      console.error("[Jobs History] Erro ao candidatar:", error)
    }
  }

  const getStatusBadge = (status: string) => {
    const statusMap: Record<string, { label: string; variant: "default" | "secondary" | "destructive" | "outline" }> = {
      pending: { label: "Pendente", variant: "outline" },
      viewed: { label: "Visualizada", variant: "secondary" },
      applied: { label: "Candidatada", variant: "default" },
      favorited: { label: "Favoritada", variant: "default" },
      dismissed: { label: "Ignorada", variant: "destructive" },
    }

    const statusInfo = statusMap[status] || { label: status, variant: "outline" }
    return <Badge variant={statusInfo.variant}>{statusInfo.label}</Badge>
  }

  const paginatedJobs = jobs.slice((currentPage - 1) * ITEMS_PER_PAGE, currentPage * ITEMS_PER_PAGE)
  const totalPages = Math.ceil(jobs.length / ITEMS_PER_PAGE)

  if (loading) {
    return (
      <div className="min-h-screen bg-gradient-to-br from-slate-950 via-slate-900 to-slate-950 flex items-center justify-center">
        <div className="text-center">
          <div className="inline-flex items-center justify-center w-12 h-12 rounded-full bg-blue-500/20 mb-4">
            <div className="w-8 h-8 border-2 border-blue-500 border-t-transparent rounded-full animate-spin"></div>
          </div>
          <p className="text-slate-400">Carregando histórico...</p>
        </div>
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-950 via-slate-900 to-slate-950 py-8">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="mb-8">
          <h1 className="text-3xl font-bold text-white mb-2">Histórico de Vagas</h1>
          <p className="text-slate-400">Visualize e gerencie todas as vagas que você recebeu</p>
        </div>

        {/* Filtros */}
        <Card className="bg-slate-800/50 border-slate-700/50 mb-6">
          <CardHeader>
            <CardTitle className="text-white flex items-center gap-2">
              <Filter className="w-5 h-5" />
              Filtros
            </CardTitle>
          </CardHeader>
          <CardContent>
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-5 gap-4">
              {/* Busca */}
              <div className="lg:col-span-2">
                <Label htmlFor="search" className="text-slate-400 mb-2 block">
                  Buscar
                </Label>
                <div className="relative">
                  <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 w-4 h-4 text-slate-400" />
                  <Input
                    id="search"
                    placeholder="Cargo, empresa ou área..."
                    value={filters.search}
                    onChange={(e) => setFilters({ ...filters, search: e.target.value })}
                    className="bg-slate-900 border-slate-700 text-white pl-10"
                  />
                </div>
              </div>

              {/* Data */}
              <div>
                <Label htmlFor="dateRange" className="text-slate-400 mb-2 block">
                  Período
                </Label>
                <Select value={filters.dateRange} onValueChange={(value) => setFilters({ ...filters, dateRange: value })}>
                  <SelectTrigger className="bg-slate-900 border-slate-700 text-white">
                    <SelectValue />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="all">Todos</SelectItem>
                    <SelectItem value="today">Hoje</SelectItem>
                    <SelectItem value="week">Últimos 7 dias</SelectItem>
                    <SelectItem value="month">Último mês</SelectItem>
                    <SelectItem value="3months">Últimos 3 meses</SelectItem>
                  </SelectContent>
                </Select>
              </div>

              {/* Empresa */}
              <div>
                <Label htmlFor="company" className="text-slate-400 mb-2 block">
                  Empresa
                </Label>
                <Select value={filters.company} onValueChange={(value) => setFilters({ ...filters, company: value })}>
                  <SelectTrigger className="bg-slate-900 border-slate-700 text-white">
                    <SelectValue placeholder="Todas" />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="all">Todas</SelectItem>
                    {companies.map((company) => (
                      <SelectItem key={company} value={company}>
                        {company}
                      </SelectItem>
                    ))}
                  </SelectContent>
                </Select>
              </div>

              {/* Área */}
              <div>
                <Label htmlFor="area" className="text-slate-400 mb-2 block">
                  Área
                </Label>
                <Select value={filters.area} onValueChange={(value) => setFilters({ ...filters, area: value })}>
                  <SelectTrigger className="bg-slate-900 border-slate-700 text-white">
                    <SelectValue placeholder="Todas" />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="all">Todas</SelectItem>
                    {areas.map((area) => (
                      <SelectItem key={area} value={area}>
                        {area}
                      </SelectItem>
                    ))}
                  </SelectContent>
                </Select>
              </div>
            </div>

            {/* Status Filter */}
            <div className="mt-4">
              <Label htmlFor="status" className="text-slate-400 mb-2 block">
                Status
              </Label>
              <Select value={filters.status} onValueChange={(value) => setFilters({ ...filters, status: value })}>
                <SelectTrigger className="bg-slate-900 border-slate-700 text-white w-full md:w-auto">
                  <SelectValue />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="all">Todos</SelectItem>
                  <SelectItem value="pending">Pendente</SelectItem>
                  <SelectItem value="viewed">Visualizada</SelectItem>
                  <SelectItem value="applied">Candidatada</SelectItem>
                  <SelectItem value="favorited">Favoritada</SelectItem>
                  <SelectItem value="dismissed">Ignorada</SelectItem>
                </SelectContent>
              </Select>
            </div>

            {/* Reset Filters */}
            {(filters.search || filters.dateRange !== "all" || (filters.company && filters.company !== "all") || (filters.area && filters.area !== "all") || filters.status !== "all") && (
              <div className="mt-4">
                <Button
                  variant="outline"
                  size="sm"
                  onClick={() =>
                    setFilters({
                      search: "",
                      dateRange: "all",
                      company: "all",
                      area: "all",
                      status: "all",
                    })
                  }
                  className="border-slate-700 text-slate-300 hover:bg-slate-700"
                >
                  <X className="w-4 h-4 mr-2" />
                  Limpar Filtros
                </Button>
              </div>
            )}
          </CardContent>
        </Card>

        {/* Tabela de Vagas */}
        <Card className="bg-slate-800/50 border-slate-700/50">
          <CardHeader>
            <CardTitle className="text-white">
              {jobs.length} {jobs.length === 1 ? "vaga encontrada" : "vagas encontradas"}
            </CardTitle>
            <CardDescription className="text-slate-400">Clique nas ações para interagir com as vagas</CardDescription>
          </CardHeader>
          <CardContent>
            {error ? (
              <div className="text-center py-8">
                <p className="text-red-400">{error}</p>
              </div>
            ) : paginatedJobs.length === 0 ? (
              <div className="text-center py-8">
                <p className="text-slate-400">Nenhuma vaga encontrada com os filtros selecionados.</p>
              </div>
            ) : (
              <>
                <div className="overflow-x-auto">
                  <Table>
                    <TableHeader>
                      <TableRow className="border-slate-700">
                        <TableHead className="text-slate-400">Data</TableHead>
                        <TableHead className="text-slate-400">Cargo</TableHead>
                        <TableHead className="text-slate-400">Empresa</TableHead>
                        <TableHead className="text-slate-400">Área</TableHead>
                        <TableHead className="text-slate-400">Localização</TableHead>
                        <TableHead className="text-slate-400">Status</TableHead>
                        <TableHead className="text-slate-400 text-right">Ações</TableHead>
                      </TableRow>
                    </TableHeader>
                    <TableBody>
                      {paginatedJobs.map((sentJob) => {
                        const job = sentJob.jobs
                        if (!job) return null

                        const isFavorited = sentJob.view_status === "favorited"

                        return (
                          <TableRow key={sentJob.id} className="border-slate-700">
                            <TableCell className="text-slate-300">
                              {format(new Date(sentJob.sent_at), "dd/MM/yyyy", { locale: ptBR })}
                            </TableCell>
                            <TableCell className="text-white font-medium">{job.title}</TableCell>
                            <TableCell className="text-slate-300">{job.company_name}</TableCell>
                            <TableCell className="text-slate-300">{job.job_area}</TableCell>
                            <TableCell className="text-slate-300">
                              {job.city ? `${job.city}, ${job.country}` : job.country}
                            </TableCell>
                            <TableCell>{getStatusBadge(sentJob.view_status)}</TableCell>
                            <TableCell className="text-right">
                              <div className="flex items-center justify-end gap-2">
                                <Button
                                  variant="ghost"
                                  size="sm"
                                  onClick={() => handleFavorite(job.id)}
                                  className={isFavorited ? "text-red-500 hover:text-red-600" : "text-slate-400 hover:text-red-500"}
                                  title={isFavorited ? "Remover dos favoritos" : "Favoritar"}
                                >
                                  <Heart className={`w-4 h-4 ${isFavorited ? "fill-current" : ""}`} />
                                </Button>
                                <Button
                                  variant="ghost"
                                  size="sm"
                                  onClick={() => handleApply(job.id, job.apply_url)}
                                  className="text-blue-400 hover:text-blue-500"
                                  title="Candidatar-se"
                                >
                                  <ExternalLink className="w-4 h-4" />
                                </Button>
                                <Button
                                  variant="ghost"
                                  size="sm"
                                  onClick={() => {
                                    setSelectedJobForFeedback({
                                      jobId: job.id,
                                      sentJobId: sentJob.id,
                                      jobTitle: job.title,
                                    })
                                    setFeedbackModalOpen(true)
                                  }}
                                  className="text-slate-400 hover:text-blue-500"
                                  title="Enviar feedback"
                                >
                                  <MessageSquare className="w-4 h-4" />
                                </Button>
                                <Button
                                  variant="ghost"
                                  size="sm"
                                  onClick={() => handleDismiss(job.id)}
                                  className="text-slate-400 hover:text-red-500"
                                  title="Ignorar"
                                >
                                  <X className="w-4 h-4" />
                                </Button>
                              </div>
                            </TableCell>
                          </TableRow>
                        )
                      })}
                    </TableBody>
                  </Table>
                </div>

                {/* Paginação */}
                {totalPages > 1 && (
                  <div className="flex items-center justify-between mt-6">
                    <div className="text-sm text-slate-400">
                      Página {currentPage} de {totalPages} ({jobs.length} {jobs.length === 1 ? "vaga" : "vagas"})
                    </div>
                    <div className="flex gap-2">
                      <Button
                        variant="outline"
                        size="sm"
                        onClick={() => setCurrentPage((p) => Math.max(1, p - 1))}
                        disabled={currentPage === 1}
                        className="border-slate-700 text-slate-300 hover:bg-slate-700"
                      >
                        <ChevronLeft className="w-4 h-4" />
                      </Button>
                      <Button
                        variant="outline"
                        size="sm"
                        onClick={() => setCurrentPage((p) => Math.min(totalPages, p + 1))}
                        disabled={currentPage === totalPages}
                        className="border-slate-700 text-slate-300 hover:bg-slate-700"
                      >
                        <ChevronRight className="w-4 h-4" />
                      </Button>
                    </div>
                  </div>
                )}
              </>
            )}
          </CardContent>
        </Card>

        {/* Feedback Modal */}
        {selectedJobForFeedback && (
          <JobFeedbackModal
            open={feedbackModalOpen}
            onClose={() => {
              setFeedbackModalOpen(false)
              setSelectedJobForFeedback(null)
            }}
            jobId={selectedJobForFeedback.jobId}
            sentJobId={selectedJobForFeedback.sentJobId}
            jobTitle={selectedJobForFeedback.jobTitle}
            onSuccess={() => {
              fetchJobs() // Recarregar dados após feedback
            }}
          />
        )}
      </div>
    </div>
  )
}
